<template>
    <div class="self-leaflet-base-layer" :style="baseStyle">
        <template v-if="this.showChangeBtn">
            <div class="base-layer-btn" v-for="(baseType,index) of showChangeBtnArr" :key="'baseType-'+index"
                @click="handleChangeBase(baseType)">
                <slot :name="baseType" :scope="baseType">
                    <div class="btn-image"
                        :class="['btn-image-'+baseType, currentBaseType==baseType?'current-active':'']">
                        <div class="btn-txt">{{baseLayerType[baseType]}}</div>
                    </div>
                </slot>
            </div>
        </template>
    </div>
</template>

<script>
    import baseLayerMixin from '../mixins/baseLayerMixin.js'
    export default {
        mixins: [baseLayerMixin],
        name: 'baseLayer',
        props: {
            baseStyle: {
                default () {
                    return {
                        position: 'absolute',
                        right: '10px',
                        top: '10px'
                    }
                }
            },
            // 是否显示切换地图的按钮，为false，即是显示一种类型的底图
            showChangeBtn: {
                default: false,
            },
            /**
             * 需要显示的底图类型，默认显示矢量图层
             * 用数组表示  ['skymapVec', 'skymapTopography', 'skymapImage']
             * skymapVec   矢量
             * skymapTopography  地形
             * skymapImage  影像
             */
            baseType: {
                type: Array,
                default () {
                    return ['skymapVec']
                }
            }
        },
        data() {
            return {
                baseLayerType: {
                    skymapVec: '矢量',
                    skymapTopography: '地形',
                    skymapImage: '影像',
                },
                currentBaseType: ''
            }
        },
        computed: {
            //确保传进来的底图类型存在我们提供的底图服务中
            showChangeBtnArr() {
                return this.baseType.filter(item => this.baseLayerType[item]);
            }
        },
        methods: {
            // 切换底图的模式
            handleChangeBase(baseType) {
                this.currentBaseType = baseType;
                this.changeBaseType(baseType);
            },
            // 得到地图的图层
            getBaseLayer() {
                return this.baseLayer;
            }
        },
        mounted() {
            if (this.showChangeBtnArr.length > 0) {
                let type = this.showChangeBtnArr[0]
                this.currentBaseType = type;
                this.changeBaseType(type);
            } else {
                this.$alert('请传入有效的底图类型', '提示');
            }
        },
    }
</script>

<style lang="less">
    .self-leaflet-base-layer {
        position: relative;
        z-index: 999;
        width: fit-content;
        background-color: #ffffff;
        padding: 4px 2px;
        box-shadow: 0 0 6px #ccc;

        .base-layer-btn {
            display: inline-block;
            margin: 0 6px;
        }

        .btn-image {
            width: 68px;
            height: 70px;
            background-image: url('../image/icon.png');
            background-repeat: no-repeat;
            background-position: -240px -50px;
            position: relative;

            .btn-txt {
                color: #ffffff;
                line-height: 24px;
                background-color: rgba(0, 0, 0, 0.5);
                position: absolute;
                width: 100%;
                left: 0;
                bottom: 0;
                text-align: center;
            }
        }

        .current-active {
            .btn-txt {
                // color: #f0f403;
                font-weight: bold;
                background-color: rgba(0, 0, 0, 0.7);
            }
        }

        .btn-image-skymapVec {
            background-position: -240px -48px;
        }

        .btn-image-skymapTopography {
            background-position: -83px -48px;
        }

        .btn-image-skymapImage {
            background-position: -162px -48px;
        }
    }
</style>