import { mapBaseLayerOption } from "../map/provideBaseLayer.js";

export default {
    data() {
        return {
            parentMap: null,
            baseLayer: null,
        }
    },
    methods: {
        changeBaseType(baseType) {
            this.baseLayer.clearLayers();
            if (mapBaseLayerOption[baseType]) {
                this.baseLayer.addLayer(mapBaseLayerOption[baseType]);
            }
            if (mapBaseLayerOption[baseType + '1']) {
                this.baseLayer.addLayer(mapBaseLayerOption[baseType + '1']);
            }
            if (mapBaseLayerOption[baseType + '2']) {
                this.baseLayer.addLayer(mapBaseLayerOption[baseType + '2']);
            }
        }
    },
    mounted() {
        this.parentMap = this.$parent;
        this.baseLayer = this.parentMap.getBaseLayer();
    }
}