import L from "leaflet";
import initMapMixin from './mixins/initMapMixin.js';

export default {
    inheritAttrs: false,
    mixins: [initMapMixin],
    props: {
        id: {
            type: String,
            default: "map",
            require: true,
        },
        defaultStyle: {
            default () {
                return {
                    border: '1px solid #ccc'
                }
            }
        },
        defaultClass: {
            default () {
                return ''
            }
        }
        // center: {
        //     custom: true,
        //     default: undefined,
        //     require: true
        // },
        // zoom: {
        //     type: Number,
        //     default: undefined,
        //     require: true,
        // },
        // maxBounds: {
        //     custom: true,
        //     default: null,
        // },
        // minZoom: {
        //     type: Number,
        //     default: undefined,
        // },
        // maxZoom: {
        //     type: Number,
        //     default: undefined,
        // },
        // attributionControl: {
        //     type: Boolean,
        //     default: true,
        // },
        // zoomControl: {
        //     type: Boolean,
        //     default: true,
        // },
    },
    data() {
        return {
            ready: false
        };
    },
    methods: {
        // 使用过程中，
        getMap() {
            return this.mymap;
        },
        getBaseLayer() {
            return this.baseLayer;
        },
        getEditLayer() {
            return this.editLayer;
        },
        getAllLayer() {
            return {
                map: this.mymap,
                baseLayer: this.baseLayer,
                editLayer: this.editLayer,
            }
        },

    },
    mounted() {
        this.ready = true;
        this.$emit('ready', this.mymap);
    }
};